import { Container, Content, ActionBar, MenuContentStyled, MenuMobile, Boni } from "./styles";
import { useState } from "react";
import { Link } from "react-router-dom";
import boniImg from "../../assets/boni-computer.webp"
import menu from "../../assets/menu.svg"
import { List } from "phosphor-react";

export function Header() {
  
  const [navbar, setNavbar] = useState(false);

  const changeBackground = () => {
    if (window.scrollY >= 80) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };

  window.addEventListener('scroll', changeBackground);

  function showMenu() {
    const menu = document.getElementById('menu') as HTMLElement;
      menu.classList.toggle('responsive');
  }
    
  return(
      <Container>
          <Content className={navbar ? 'navbar active' : 'navbar'}>
          <Boni>Boni</Boni>

            <MenuContentStyled>
              <button
                id="button-hamburger"
                type="button"
                onClick={showMenu}>
                <List size={32} weight="bold" />
              </button>
            </MenuContentStyled>

              <ActionBar>
                <Link to="/"  >Home</Link>
                <Link to="/about">About</Link>
                <Link to="/projects">Projects</Link>
              </ActionBar>
              
          </Content>

              <MenuMobile id="menu">
                <Link onClick={showMenu} to="/" >Home</Link>
                <Link onClick={showMenu} to="/about">About</Link>
                 <Link onClick={showMenu} to="/projects">Projects</Link>
              </MenuMobile>
      </Container>
  );
}